import { Alert, Callout } from "adminlte-2-react";
import React from "react";
import { connect } from "react-redux";
import { dismissGlobalError } from "src/utils/storeHelper";
import Dialog from "./Dialog";

const GlobalToast = ({
    globalMessage = {}
}) => {
    if (!globalMessage || !globalMessage.message) {
        return <React.Fragment />
    }
    return (
        <div className="globalAlert">
            <Alert
                closable={true}
                title={globalMessage.title}
                size='xl'
                show={globalMessage.message}
                zIndex={9999}
                type={globalMessage.type}
                onDismiss={() => {
                    dismissGlobalError();
                }}>
                <p>{globalMessage.message}</p>
            </Alert>
        </div>
    )

}

const mapStateToProps = (state) => {
    return {
        globalMessage: state.globalState?.globalMessage || {}
    };
};


export default connect(mapStateToProps, null)(GlobalToast);
