import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file
import { isBefore, isDate, subDays, subYears } from "date-fns";
import React, { useEffect, useMemo } from "react";
import { useState } from "react";
import { convertToDate, formatMySql } from "src/utils/commonHelper";
import { parseQueryParams, pushQueryParams } from "src/utils/routerHelper";
import { DateRange } from 'react-date-range';
import { Button } from 'adminlte-2-react';
import Dialog from '../Dialog';

const DateFilter = ({
    label = "Filter by date",
    cols = 12,
    defaultDaysSelected = 10,
    ...rest
}) => {
    const [showPicker, setShowPicker] = useState(false)
    const [dateStr, setDateStr] = useState(`Previous ${defaultDaysSelected} days`)
    const [state, setState] = useState([]);

    const getFilterValue = () => {
        const defaultStart = new Date()
        const defaultEnd = subDays(new Date(), defaultDaysSelected)
        let start = parseQueryParams(rest.history, 'start', null)
        let end = parseQueryParams(rest.history, 'end', null)

        if (!start || !end) {
            setDateStr(`Previous ${defaultDaysSelected} days`)
        }

        start = isDate(start) ? convertToDate(start) : defaultStart
        end = isDate(end) ? convertToDate(end) : defaultEnd

        setState([
            {
                startDate: start,
                endDate: end,
                key: 'selection'
            }
        ])
    }

    const setDatesValue = (item) => {
        setState([item.selection])
        const { startDate, endDate } = item.selection
        if (startDate && endDate) {
            setDateStr(`${formatMySql(startDate)} to ${formatMySql(endDate)}`)
        }
    }

    useEffect(() => {
        getFilterValue();
        const unlisten = rest.history.listen(() => {
            getFilterValue();
        })
        return unlisten
    }, [])

    useEffect(() => {
        document.onclick = function (e) {
            const container = document.querySelector('.datePickerComponent-active')
            if (container && !container.contains(e.target)){
                setShowPicker(false)
            }
        }
    }, [])

    return (
        <div className={`datePickerComponent${showPicker ? '-active': ''} col-md-${cols}`}>
            <Button disabled text={`Showing results from ${dateStr}`} />
            <Button type="success" onClick={() => setShowPicker(true)} icon="fa-calendar" text="Filter by Date" />
            <div className={`datePicketContainer ${showPicker ? '' : 'hidden'}`}>
                {state.length > 0 && <DateRange
                    minDate={subYears(new Date(), 1)}
                    maxDate={new Date()}
                    onChange={setDatesValue}
                    showSelectionPreview={false}
                    moveRangeOnFirstSelection={false}
                    ranges={state}
                />}
                <Button onClick={() => {
                    setShowPicker(false)
                    pushQueryParams(rest.history, {
                        start: formatMySql(state[0].startDate),
                        end: formatMySql(state[0].endDate)
                    })

                }} className="filterByDates" type="success" text="Filter results" />
            </div>
        </div>
    )
}

export default DateFilter