import { Alert } from "adminlte-2-react"
import { useEffect, useState } from "react"
import { connect } from "react-redux"
import CommonService from "src/services/CommonService"

const triggerFetchNotifications = async (onFetch = (notifications) => { }) => {
    const data = await CommonService.getNotifications()
    return data?.success ? onFetch(data.data) : []
}

const GlobalNotifications = ({
    currentUser,
}) => {
    const [notifications, setNotifications] = useState([])

    useEffect(() => {
        triggerFetchNotifications(setNotifications)
    }, [currentUser])

    useEffect(() => {
        const interval = setInterval(() => triggerFetchNotifications(setNotifications), 2000)
        return () => clearInterval(interval)
    }, [currentUser])
    if (!notifications || notifications.length < 1 || !currentUser || !currentUser?.id) {
        return <></>
    }
    return (
        <div className="notification-alert-box">
            {
                notifications.length > 0 && notifications.map(n => {
                    return (
                        <Alert key={n.message} title={n.message} icon={`fa-bell`} style={{ borderRadius: '0 !important' }} type={n.type} >
                            <a className="visit" href={n.path} title={"View details"} >View details</a>
                        </Alert>
                    )
                })
            }
        </div>
    );
}

const mapStateToProps = (state) => {
    return {
        currentUser: state.globalState?.currentUser || null,
    };
};



export default connect(mapStateToProps, null)(GlobalNotifications);
