import * as yup from "yup";
import { NmsDeviceActions } from "src/slices/NmsDeviceSlice";
import { Box, Button, Col, Content, Row } from "adminlte-2-react";
import DataGridComponent from "src/components/DataGridComponent";
import { useEffect, useState } from "react";
import CommonService from "src/services/CommonService";
import { connect } from "react-redux";
import { SETTINGS_CONFIG } from "src/constants/global";
import LocationFilter from "src/components/filters/LocationFilter";
import ClearFilterButton from "src/components/filters/ClearFilter";
import PhotoThumb from "src/components/PhotoThumb";

const NmsDeviceListView = ({
  title = 'Manage NMS Devices',
  description = 'here you can manage devices',
  settings = [],
  ...rest
}) => {
  const [deviceStatus, setDeviceStatus] = useState({})
  const [checkInterval, setCheckInterval] = useState(5000)

  useEffect(() => {
    const interval = settings.find(s => s.name === SETTINGS_CONFIG.SNMP_CHECK_INTERVAL)?.value || 5
    setCheckInterval(interval)
  }, [])

  const [locations, setLocations] = useState([])
  useEffect(() => {
    (async () => {
      setLocations(await CommonService.getLocationList())
    })();
  }, [])


  useEffect(() => {
    const interval = setInterval(async () => {
      const data = await CommonService.getNmsDevicesStatus()
      data?.success && setDeviceStatus(data.data)
    }, checkInterval * 1000)
    return () => clearInterval(interval)
  }, [checkInterval])

  return (
    <Content title={title} subTitle={description} browserTitle={title}>
      <Box type="primary">
        <Row>
          <Col md={12}>
            <DataGridComponent
              {...rest}
              entityPlural="NMS Devices"
              entitySingle="NMS Device"
              resource={`/api/v1/admin/nms-devices`}
              onDataUpdated={async (tableData) => {
                NmsDeviceActions.setNmsDevices(tableData)
              }}
              filterComponents={[
                <LocationFilter history={rest.history} key={'location_filter'} cols={4} />,
                <ClearFilterButton history={rest.history} key={'clearfilter'} />
              ]}
              columns={[
                {
                  dataField: 'id',
                  text: 'Device ID',
                  hidden: true,
                }, {
                  dataField: 'name',
                  text: 'Name',
                  sort: true,
                }, 
                
                {
                  dataField: 'locationId',
                  text: 'Location',
                  sort: true,
                  formatter: (c, row) => {
                    return locations.find(l => `${l.id}` === `${row.locationId}`)?.title || '-'
                  }
                },
                {
                  dataField: 'ip',
                  text: 'IP:Port',
                  formatter: (c, row) => {
                    let address = 'http://'+ row['ip'] + (row['port'] ? `:${row['port']}`: '') 
                    return <a target="_blank" href={address}>{address}</a>
                  }
                },
                {
                  dataField: "image",
                  text: "",
                  formatter: (c, row) => {
                    const online = deviceStatus[row.ip]?.isOnline
                    return (
                      <>
                        <div className="deviceStatusSection text-center">
                          <PhotoThumb url={row.image} width={100} height={50} />
                            {online && <Button type="success" icon="fa-check" />}
                            {!online && <Button type="danger" icon="fa-times" />}
                          <strong className="text-center" style={{ color: online ? '#0f0' : '#f00' }}>{online ? 'Online' : 'Offline'}</strong>
                        </div>
                      </>
                    );
                  }
                },
                {
                  dataField: 'sysName',
                  text: 'Device System Name',
                  width:100,
                  formatter: (c, row) => {
                    const str = deviceStatus[row.ip]?.systemName
                    try{
                      return atob(str).split(",").join("\n")
                    }catch(e){
                      return "Unknown"
                    }
                  }
                }
              ]}
              formOptions={
                {
                  fields: [
                    {
                      id: "ip",
                      name: "ip",
                      label: "IP",
                      type: "text",
                      cols: 10,
                      required: true,
                    },
                    {
                      id: "port",
                      name: "port",
                      label: "Port",
                      type: "text",
                      cols: 2,
                    },
                    {
                      id: "name",
                      name: "name",
                      label: "Device Name",
                      type: "text",
                      required: true,
                      cols: 12,
                    },
                    {
                      id: "image",
                      name: "image",
                      label: "Device Icon/Image",
                      type: "upload",
                      required: true,
                      cols: 12,
                    },
                    {
                      id: "locationId",
                      name: "locationId",
                      label: "Location",
                      type: "select",
                      required: true,
                      options: locations.map(l => {
                        return {
                          key: l.id,
                          value: l.title
                        }
                      }),
                      cols: 12,
                    },
                  ],
                  validationSchema: yup.object().shape({
                    ip: yup.string().required(),
                    name: yup.string().required(),
                  }),
                }
              }
            />
          </Col>
        </Row>
      </Box>
    </Content>
  );
};

const mapStateToProps = (state) => {
  return {
    settings: state.globalState?.settings || []
  };
};

export default connect(mapStateToProps, null)(NmsDeviceListView);
