import { Box } from "adminlte-2-react";
import { Field, Form, Formik } from "formik";
import React, { useState } from "react";
import { useHistory } from "react-router";
import { ucFirst } from "src/utils/commonHelper";
import * as yup from "yup";

const loginSchema = yup.object().shape({
  username: yup.string().required(),
  password: yup.string().required(),
});

const LoginForm = ({ triggerLogin = async () => { }, history, ...rest }) => {
  const _history = useHistory()
  return (
    <>
      <div className="fixed-logo-area">
        <img className="leftLogo" src="/dp_logo.png" />
        <img className="rightLogo" src="/dp_logo.png" />
      </div>
      <div className="login-box" style={{ border: 'solid 1px #eee', marginBottom: '0 !important', paddingTop: 50 }}>
        <div className="login-logo">
          <a href="/">
            <b>TMCS</b>
            <span style={{ paddingLeft: 5 }}>Login</span>
          </a>
        </div>
        <div className="login-box-body">
          <Box type="primary" title="Login to continue">

            <Formik
              initialValues={{
                username: "",
                password: "",
              }}
              onSubmit={async (values, { setErrors }) => {
                const data = await triggerLogin(values, history || _history)
                if (data?.errors) {
                  setErrors(data.errors)
                }
              }}
              validationSchema={loginSchema}
            >
              {({ errors, touched }) => (
                <Form>
                  <div className="card-body">
                    <div className="mb-2">
                      <label htmlFor="email" className="form-label">
                        Username
                      </label>
                      <Field
                        type="text"
                        autoComplete="off"
                        className={
                          errors.username && touched.username
                            ? "form-control shadow-none is-invalid"
                            : "form-control shadow-none"
                        }
                        placholder="Enter Username"
                        name="username"
                        id="username"
                      />
                      {errors.username && touched.username && (
                        <div className="invalid-feedback text-red"><strong>Username</strong> - {ucFirst(errors.username)}</div>
                      )}
                    </div>
                    <div className="mb-2">
                      <label htmlFor="password" className="form-label">
                        Password
                      </label>
                      <Field
                        type="password"
                        name="password"
                        autoComplete="off"
                        className={
                          errors.password && touched.password
                            ? "form-control shadow-none is-invalid"
                            : "form-control shadow-none"
                        }
                        placholder="Enter password"
                        id="password"
                      />
                      {errors.password && touched.password && (
                        <div className="invalid-feedback">{errors.password}</div>
                      )}
                    </div>
                  </div>
                  <hr />
                  <div className="card-footer text-center">
                    <div className="d-flex flex-row-reverse">
                      <button type="submit" className="btn btn-block rounded-0  btn-primary">
                        Submit
                      </button>
                    </div>
                  </div>
                </Form>
              )}
            </Formik>
          </Box>
        </div>
      </div>
      <div className="loginFooter login-box">
        <div>
          <img className="footerLogo" src="/default_logo.png" /><br />
          <h4>Mobile Communications India Private Limited.</h4>
          <p>A-16, (Front Side), Naraina Industrial Area, Phase II<br />New Delhi - 110028,
            Email: <a href="mailto:contact@mobilecom.in">contact@mobilecom.in</a>
          </p>
        </div>
      </div>
    </>
  )
};

export default LoginForm;
