import { connect } from "react-redux";
import UserService from "src/services/UserService";
import AdminLTE, { Alert, Navbar, Sidebar, } from "adminlte-2-react";
import LoginForm from "src/components/LoginForm";
import { BrowserRouter } from "react-router-dom";
import { navigateToLogin } from "src/utils/routerHelper";
import {
  BRANCH_LIST_PAGE,
  LOCATION_LIST_PAGE,
  NAV_ITEMS,
  RANK_LIST_PAGE,
  REGISTRATION_LIST_PAGE,
  USER_LIST_PAGE,
  DASHBOARD_ROUTE,
  LOGOUT_ROUTE,
  NMS_LIST_PAGE,
  DEVICE_MAMAGEMENT_ROUTE,
  ONLINE_USERS_ROUTE,
  BACKUPS_LIST,
  REPORTS_LIST_PAGE,
  SETTINGS_PAGE,
  CONTACT_PAGE,
  SETTINGS_CONFIG,
  DORMANT_USERS_PAGE
} from "src/constants/global";

import React, { useEffect, useState } from "react";
import BranchListView from "src/modules/branches/views/BranchListView";
import LocationListView from "src/modules/locations/views/LocationListView";
import RankListView from "src/modules/ranks/views/RankListView";
import RegistrationListView from "src/modules/registrations/views/RegistrationListView";
import UserListView from "src/modules/users/UserListView";
import DashboardView from "src/modules/dashboard/views/DashboardView";
import GlobalToast from "src/components/GlobalToast";
import NmsDeviceListView from "src/modules/nms-devices/views/NmsDevicesListView";
import DeviceListView from "src/modules/device-directory/views/DevicesListView";
import OnLineUsersView from "src/modules/users/OnlineUsersView";
import BackupsListView from "src/modules/backups/BackupsListView";
import ReportListView from "src/modules/reports/ReportListView";
import SettingsListView from "src/modules/settings/SettingsListView";
import ContactUsView from "src/modules/contact_us/ContactUsView";
import { format } from "date-fns";
import SettingService from "src/services/SettingService";
import { GlobalActions } from "src/slices/globalSlice";
import CommonService from "src/services/CommonService";
import { formatUserName, humanFriendlyDateFormat } from "src/utils/commonHelper";
import GlobalNotifications from "src/components/GlobalNotifications";
import DormantUsersView from "src/modules/users/DormantUsersView";

const { Entry } = Navbar

const Footer = () => {
  const title = ` ${format(new Date(), 'yyyy')} MCIL. All Rights Reserved.`
  return (
    <div className="text-center" style={{ color: '#444', padding: "15px 0", margin: 0, marginLeft: 200, background: '#eee', borderTop: 'solid 1px #ccc' }}>
      <p><strong>{title}</strong></p>
    </div>
  )
}

const Layout = ({ 
  triggerLogin,
  triggerGetCurrentUser,
  currentUser,
}) => {

  useEffect(() => {
    triggerGetCurrentUser()
  }, [])

  const isLoggedIn = !!currentUser
  if (!isLoggedIn) {
    return (
      <BrowserRouter>
        <GlobalToast />
        <LoginForm triggerLogin={triggerLogin} />
      </BrowserRouter>
    )
  }
  return (
    <React.Fragment>
      <GlobalNotifications currentUser={currentUser} />
      <GlobalToast />
      <AdminLTE footer={<Footer />} theme="black" title={[]}>
        <Navbar.Core>
          <Entry to={LOGOUT_ROUTE} labelValue="Logout" icon="fa-power-off" labelType="danger" onClick={() => navigateToLogin()} />
        </Navbar.Core>
        <Sidebar.Core>
          <a className="logo" href="/">
            <img src="/dp_logo.png" style={{width: '100%', padding: '30px 50px', height: 'inherit', background: '#fff'}} />
          </a>
          {
            NAV_ITEMS.map((item, index) => {
              if (item.items && item.items.length > 0) {
                return (<Sidebar.Item onClick={item.onClick} key={index} icon={item.icon} text={item.title} to={item.path} color={item.color ?? 'gray'}>
                  {(item.items || []).map((childItem, childIndex) => {
                    return <Sidebar.Item onClick={childItem.onClick} key={`${index}-child-${childIndex}`} icon={childItem.icon} text={childItem.title} to={childItem.path} color={childItem.color ?? 'gray'} />
                  })}
                </Sidebar.Item>)
              }
              return <Sidebar.Item onClick={item.onClick} key={`${index}`} icon={item.icon} text={item.title} to={item.path} color={item.color ?? 'gray'} />
            })
          }
          <div className="userLoggedInInfo" text="Admin" >
            <p>{formatUserName(currentUser)}<br />
              <span>{humanFriendlyDateFormat(currentUser.lastLogin, 'dd-MM-yyyy hh:mm a')}</span>
            </p>
          </div>
        </Sidebar.Core>
        <BranchListView currentUser={currentUser} path={BRANCH_LIST_PAGE} exact={true} />
        <LocationListView currentUser={currentUser} path={LOCATION_LIST_PAGE} exact={true} />
        <RankListView currentUser={currentUser} path={RANK_LIST_PAGE} exact={true} />
        <RegistrationListView currentUser={currentUser} path={REGISTRATION_LIST_PAGE} exact={true} />
        <UserListView currentUser={currentUser} path={USER_LIST_PAGE} exact={true} />
        <DashboardView currentUser={currentUser} path={DASHBOARD_ROUTE} exact={true} />
        <NmsDeviceListView currentUser={currentUser} path={NMS_LIST_PAGE} exact={true} />
        <DeviceListView currentUser={currentUser} path={DEVICE_MAMAGEMENT_ROUTE} exact={true} />
        <OnLineUsersView currentUser={currentUser} path={ONLINE_USERS_ROUTE} exact={true} />
        <DormantUsersView currentUser={currentUser} path={DORMANT_USERS_PAGE} exact={true} />
        <BackupsListView currentUser={currentUser} path={BACKUPS_LIST} exact={true} />
        <ReportListView currentUser={currentUser} path={REPORTS_LIST_PAGE} exact={true} />
        <SettingsListView currentUser={currentUser} path={SETTINGS_PAGE} exact={true} />
        <ContactUsView currentUser={currentUser} path={CONTACT_PAGE} exact={true} />
      </AdminLTE>
    </React.Fragment>

  );
};

const mapStateToProps = (state) => {
  return {
    currentUser: state.globalState?.currentUser || null,
    settings: state.globalState?.settings || null,
    notifications: state.globalState?.notifications || []
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    triggerLogin: async ({ username, password }, history) => {
      const data = await UserService.loginUser(username, password);
      data?.success && dispatch(GlobalActions.setUserState(data.data));
      return data;
    },
    triggerGetCurrentUser: async () => {
      const data = await UserService.getCurrentUser()
      data?.success && dispatch(GlobalActions.setCurrentUser(data.data))
      if (data?.success){
        const data = await SettingService.getWebsiteSettings()
        data?.success && dispatch(GlobalActions.setWebsiteSettings(data.data))  
      }
    },
    triggerFetchNotifications: async (user) => {
      if (!user){
        return
      }
      const data = await CommonService.getNotifications()
      data?.success && dispatch(GlobalActions.setNotifications(data.data ?? []))  
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Layout);
