const { default: httpClient } = require("src/utils/httpClient");

const getLocationList = async () => {
    const data = await httpClient.get("/api/v1/locations");
    const locations = data?.success ? data.data : []
    return locations
}

const getBranchesList = async () => {
    const data = await httpClient.get("/api/v1/branches");
    return data?.success ? data.data : []
}


const getRanksList = async () => {
    const data = await httpClient.get("/api/v1/ranks");
    return data?.success ? data.data : []
}

const getSubLocations = async (locationId) => {
    if (locationId && locationId > 0){
        const data = await httpClient.get(`/api/v1/admin/locations?locationId=${locationId}`);
        if (data?.success) {
            return data.data.results;
        }    
    }
    return []
}


const createDatabaseBackup = async () => await httpClient.post(`/api/v1/admin/db-backups`)
const getDashboardSummary = async () => await httpClient.get(`/api/v1/admin/dashboard`)
const getNmsDevicesStatus = async () => await httpClient.get(`/api/v1/admin/nms-devices/status`)
const getNotifications = async () => await httpClient.get(`/api/v1/admin/notifications`)
const CommonService = {
    getLocationList,
    getBranchesList,
    getRanksList,
    createDatabaseBackup,
    getDashboardSummary,
    getNmsDevicesStatus,
    getNotifications,
    getSubLocations
}
export default CommonService
