import { Alert, Badge, Box, Col, Content, Row } from "adminlte-2-react";
import DataGridComponent from "src/components/DataGridComponent";
import { useEffect, useMemo, useState } from "react";
import CommonService from "src/services/CommonService";
import UserService from "src/services/UserService";
import { USER_ROLES, USER_STATUSES, USER_STATUS_INFO } from "src/constants/global";
import Dialog from "src/components/Dialog";
import EntityForm from "src/components/EntityForm";
import { showGlobalModalNotification } from "src/utils/storeHelper";


const resource = `/api/v1/admin/users`

const UserListView = ({
  currentUser,
  title = 'Users',
  description = 'here you can manage users',
  ...rest
}) => {

  const [locations, setLocations] = useState([])
  const [branches, setBranches] = useState([])
  const [ranks, setRanks] = useState([])
  const [record, setRecord] = useState(null)
  const [showPasswordDialog, setShowPasswordDialog] = useState(false)
  const [formErrors, setFormErrors] = useState({})

  const allowedUserRolesToCreate = useMemo(() => {
    return [USER_ROLES.USER, USER_ROLES.ADMIN];
  }, [currentUser])

  useEffect(() => {
    (async () => {
      setLocations(await CommonService.getLocationList())
      setBranches(await CommonService.getBranchesList())
      setRanks(await CommonService.getRanksList())
    })();
  }, [])

  const fields = useMemo(() => {
    const _fields = [
      {
        id: "username",
        name: "username",
        label: "Username",
        required: true,
        placeholder: "Enter Username",
        type: "text",
        cols: 12,
      },
      {
        id: "role",
        name: "role",
        label: "User Role",
        type: "radio_group",
        required: true,
        default: USER_ROLES.USER,
        cols: 12,
        options: allowedUserRolesToCreate.map(role => {
          return {
            key: role,
            value: role
          }
        })
      },
      {
        id: "firstName",
        name: "firstName",
        label: "First Name",
        required: true,
        type: "text",
        cols: 4,
      },
      {
        id: "middleName",
        name: "middleName",
        label: "Middle Name",
        type: "text",
        cols: 4,
      },
      {
        id: "lastName",
        name: "lastName",
        label: "Last Name",
        type: "text",
        cols: 4,
      },
      
      {
        id: "locationId",
        name: "locationId",
        label: "Location",
        required: true,
        type: "select",
        options: locations.map(l => {
          return {
            key: l.id,
            value: l.title
          }
        }),
        cols: 4,
      },
      {
        id: "branchId",
        name: "branchId",
        label: "Branch",
        required: true,
        type: "select",
        options: branches.map(l => {
          return {
            key: l.id,
            value: l.title
          }
        }),
        cols: 4,
      },
      {
        id: "rankId",
        name: "rankId",
        label: "Rank",
        required: true,
        type: "select",
        options: ranks.map(l => {
          return {
            key: l.id,
            value: l.title
          }
        }),
        cols: 4,
      },
      {
        id: "ip",
        name: "ip",
        label: "IP",
        type: "text",
        cols: 10,
      },
      {
        id: "port",
        name: "port",
        label: "Port",
        type: "text",
        cols: 2,
      },
      {
        id: "addressLine1",
        name: "addressLine1",
        label: "Address Line 1",
        type: "textarea",
        cols: 6,
      },
      {
        id: "addressLine2",
        name: "addressLine2",
        label: "Address Line 2",
        type: "textarea",
        cols: 6,
      },
    ]
    if (record && record?.id) {
      // return _fields.filter(field => !['password', 'username'].includes(field.name))
    }

    return _fields
  }, [locations, ranks, branches, record, allowedUserRolesToCreate])

  return (
    <Content title={title} subTitle={description} browserTitle={"Manage Users"}>
      {record?.id && (
        <Dialog
          title={`Updating password #${record.id}`}
          show={showPasswordDialog}
          onDismiss={() => {
            setShowPasswordDialog(false)
          }}
        >
          <>
          {record.status === USER_STATUSES.APPROVED && (
            <p className="text-danger">
              <strong>Please Note:*</strong>
              &nbsp;Setting password for approved users will activate the user (they can login).
              <hr />
            </p>
          )}
          <EntityForm
            entitySingle={'Password'}
            initialValues={{
              password: '',
              confirmPassword: '',
            }}
            formErrors={formErrors}
            fields={[
              {
                id: "password",
                name: "password",
                label: "Password",
                type: "password",
                required: true,
                cols: 12,
              },
              {
                id: "confirmPassword",
                name: "confirmPassword",
                label: "Confirm Password",
                type: "password",
                required: true,
                cols: 12,
              },
            ]}
            editMode
            onSubmit={async (values, { resetForm }) => {
              window.scrollTo(0, 0);
              const { password, confirmPassword } = values
              const data = await UserService.updatePassword(record.id, password, confirmPassword)
              if (data?.success) {
                resetForm();
                setRecord(null);
                setShowPasswordDialog(false);
                showGlobalModalNotification({
                  success: true,
                  message: `Password successfully updated`
                })
                document.getElementById('btnRefresh')?.click()
              } else {
                setFormErrors(data?.errors ?? {})
              }
            }}
          />
          </>
        </Dialog>
      )}
      <Box type="primary" title="">
        <Row>
          <Col md={12}>
            {Object.keys(USER_STATUS_INFO).map(s => (
              <p>
                <span style={{width: 75, display: 'inline-block'}}><Badge color={USER_STATUS_INFO[s].COLOR} text={s} /></span> : <strong>{USER_STATUS_INFO[s].TEXT}</strong>
              </p>
            ))}

            <DataGridComponent
              modalSize="lg"
              actionColumnWidth={420}
              {...rest}
              canAdd={true}
              canDeleteFn={(row) => row?.id !== currentUser?.id}
              beforeSave={(values) => {
                if (!values['role']){
                  values['role'] = USER_ROLES.USER
                }
                return values;
              }}
              rowActionButtons={[
                {
                  buildText: (row) => {
                    return row?.status === USER_STATUSES.APPROVED ? 'Create Password'  : 'Update Password'
                  },
                  icon: 'fa-key',
                  type: 'primary',
                  onClick: async (id, row) => {
                    setRecord(row)
                    setShowPasswordDialog(true)
                  }
                },
                {
                  text: 'Block',
                  icon: 'fa-times',
                  type: 'danger',
                  canShow: (row) => {
                    return row.status === USER_STATUSES.ACTIVE && currentUser.id !== row.id
                  },
                  onClick: async (id) => {
                    await UserService.blockUser(id)
                  }
                },
                {
                  text: 'Unblock',
                  icon: 'fa-check',
                  type: 'success',
                  canShow: (row) => {
                    return row.status === USER_STATUSES.DISABLED
                  },
                  onClick: async (id) => {
                    await UserService.unblockUser(id)
                  }
                }
              ]}
              entitySingle={"User"}
              entityPlural="Users"
              resource={resource}
              onFormShow={(row, mode) => {
                setRecord(row)
              }}
              formOptions={
                {
                  fields,
                }
              }
              columns={[
                {
                  dataField: 'id',
                  text: 'User ID',
                  hidden: true,
                }, {
                  dataField: 'username',
                  text: 'Username',
                  sort: true,
                },
                {
                  dataField: 'role',
                  text: 'Role',
                  sort: true
                },
                {
                  dataField: 'branchId',
                  text: 'Branch',
                  sort: true,
                  formatter: (c, row) => {
                    return branches.find(b => `${b.id}` === `${row.branchId}`)?.title || '-'
                  }
                },
                {
                  dataField: 'rankId',
                  text: 'Rank',
                  sort: true,
                  formatter: (c, row) => {
                    return ranks.find(r => `${r.id}` === `${row.rankId}`)?.title || '-'
                  }
                },
                {
                  dataField: 'locationId',
                  text: 'Location',
                  sort: true,
                  formatter: (c, row) => {
                    return locations.find(l => `${l.id}` === `${row.locationId}`)?.title || '-'
                  }
                },
                {
                  dataField: 'status',
                  text: 'Status',
                  sort: true,
                  formatter: (c, row) => {
                    return <Badge text={row.status} color={row.status === USER_STATUSES.ACTIVE ? 'green': 'orange'} />
                  }
                },
                {
                  dataField: 'firstName',
                  text: 'Name',
                  sort: true,
                  formatter: (c, row) => {
                    return [row.firstName, row.middleName, row.lastName].filter(x => !!x).join(" ")
                  }

                },
                {
                  dataField: 'ip',
                  text: 'IP:Port',
                  formatter: (c, row) => {
                    return [row.ip, row.port].filter(x => !!x).join(":")
                  }
                },
              ]}
            />
          </Col>
        </Row>
      </Box>
    </Content>
  );
};

export default UserListView;
