import { Badge } from "adminlte-2-react";
import { useEffect, useRef, useState } from "react";
import CommonService from "src/services/CommonService";

const LocationTitleComponent = ({
    row
}) => {
    const ref = useRef();
    const [sublocations, setSubLocations] = useState([])

    useEffect(() => {
        ref.current = true
        CommonService.getSubLocations(row?.id).then(locations => {
            if (ref.current){
                setSubLocations(locations)
            }
        })
        return () => { ref.current = false }
    }, [])

    return (
        <>
            <span style={{ padding: 5 }}>{row.title}</span>
            {sublocations.length > 0 && <Badge color="green" text={`${sublocations.length} subLocation${sublocations.length > 1 ? 's':''}`} />}
        </>
    )
}

export default LocationTitleComponent;