import { createSlice } from "@reduxjs/toolkit";
import { CURRENT_USER_KEY } from "src/constants/global";
import LocalStorageHelper from "src/utils/localStorageHelper";

export const { actions: GlobalActions, reducer: GlobalReducer } = createSlice({
  name: "globalSlice",
  initialState: {
    errors: null,
    globalMessage: {
      title: '',
      type: '',
      message: ''
    },
    settings: [],
    notifications: [],
    snmpDeviceStatus: {},
    user: LocalStorageHelper.get(CURRENT_USER_KEY),
    currentUser: null,
  },
  reducers: {
    setGlobalMessage: (state, { payload }) => {
      const { type, title, message } = payload
      state.globalMessage = { ...state.globalError, type, title, message }
    },
    setWebsiteSettings: (state, { payload }) => {
      const { results } = payload
      state.settings = results
    },
    setSnmpDeviceStatus: (state, { payload }) => {
      state.snmpDeviceStatus = payload
    },
    setNotifications: (state, { payload }) => {
      state.notifications = payload
    },
    setUserState: (state, { payload }) => {
      payload
        ? LocalStorageHelper.store(CURRENT_USER_KEY, payload)
        : LocalStorageHelper.delete(CURRENT_USER_KEY);
      state.user = payload;

      if (payload) {
        window.location = '/'
      }
    },
    setCurrentUser: (state, {payload}) => {
      state.currentUser = payload
    }
  },
});
